.WelcomeContainer{
    padding: 3em 1.5em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    height: 100vh;
    background-color:#f2f0e4;
}
.WelcomeContainer h1{
    font-size: 1.7em;
    font-weight: 500;
    font-style: normal;
    margin-bottom: 0;
    color:var(--textColor);
}

.WelcomeInfo{
    height: 253px;
    overflow: auto;
}
.WelcomeInfo p {
    margin-bottom: 2em;
    color: var(--textColor);
    font-size: 1em;
    font-weight: 400;
    font-style: normal;
}

.actionBtn{
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.actionBtn > a:last-child{
    color: var(--btnBgColor);
    font-size: 0.875;
    font-weight: 400;
    font-style: normal;
}